export const getAll = async () => {
  const all = []
  const spirit = await getSpirit()
  const opportunity = await getOpportunity()
  const curiosity = await getCuriosity()
  const apod = await getApod()
  for (let arr of [apod,spirit,opportunity,curiosity]){
    arr.map((item) => 
    {
      all.push(item)
    })
  }
  return all
}
