import { Component } from "react";
import { Link } from "react-router-dom";
class Grid extends Component{
    render(){
        const data = this.props.data
        const postlink = `/user/`
        return(
            <div className="profile-posts" >
                {data.map((item) => {
                    return <a className="profile-post-link">
                    {item.type === "video" ? 
                    <div className="profile-post-item"  ><iframe src={item.url}></iframe></div>:
                    <Link to={postlink+item.user+'/'+item.id}><div className="profile-post-item" style={{backgroundImage: `url(${item.url})`}}></div></Link>
                    }
                </a>
                })}
              </div>         
        )
    }
}

export default Grid